import 'animate.css/animate.min.css';
import Board from 'pages/Board';
import Spot from 'pages/Spot';
import Listings from 'pages/Listings';
import Newlisting from 'pages/Newlisting';
import Recurring from 'pages/Recurring';
import Listing from 'pages/Reqs';
import Applications from 'pages/Applications';
import Kcsissues from 'pages/Kcsissues';
import React from 'react';
import { Route, Switch } from 'react-router-dom';
import { cssTransition, ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import Home from './pages/Home';
import Contract from './pages/Contract';
import KCSMessages from './pages/KCSMessages';
import Diligence from './pages/Diligence';
import Assessments from './pages/Assessments';
import ProtectedRoute from './components/ProtectedRoute';

const slideUp = cssTransition({
  enter: 'animate__animated animate__slideInUp',
  exit: 'animate__animated animate__slideOutDown',
});

function App() {
  var router = (
    <ProtectedRoute>
      <Switch>
        <Route path="/" exact component={Listing} />
        <Route path="/home" exact component={Home} />
        <Route path="/contract" exact component={Contract} />
        <Route path="/diligence" exact component={Diligence} />
        <Route path="/assessments" exact component={Assessments} />
        <Route path="/kcsissues" exact component={Kcsissues} />
        <Route path="/board" exact component={Board} />
        <Route path="/spot" exact component={Spot} />
        <Route path="/listings" exact component={Listings} />
        <Route path="/newlisting" exact component={Newlisting} />
        <Route path="/recurring" exact component={Recurring} />
        <Route path="/kcsmessages" exact component={KCSMessages} />
        <Route path="/status" exact component={Listing} />
        <Route path="/applications" exact component={Applications} />
      </Switch>
    </ProtectedRoute>
  );
  return (
    <>
      {router}
      <ToastContainer
        position="bottom-right"
        autoClose={5000}
        hideProgressBar
        newestOnTop
        closeOnClick
        rtl={false}
        transition={slideUp}
        pauseOnFocusLoss
        draggable
        pauseOnHover
      />
    </>
  );
}

export default App;
