import LeftMenu from 'components/LeftMenu';
import TopFilter from 'components/TopFilter';
import { default as DefaultProjectImg } from 'assets/images/spotlight07.jpg';
import { useState } from 'react';
import IssueBoard from './IssueBoard';

function Listings() {
  const [showMenu, setShowMenu] = useState(false);
  return (
    <div className="flex w-screen h-screen overflow-y-hidden">
      <LeftMenu showMenu={showMenu} onCloseMenu={() => setShowMenu(false)} />
      <div className="flex flex-col flex-1 overflow-hidden">
        <TopFilter
          onOpenMenu={() => setShowMenu(!showMenu)}
          title="Listing Schedule"
        />
        <img
        src={DefaultProjectImg}
          className="p-2 bg-gray-100 w-full overflow-hidden"
          alt="project-dijets"
        />
        <IssueBoard />
      </div>
    </div>
  );
}

export default Listings;
