import { ReactComponent as AttachmentIcon } from 'assets/icons/attachment.svg';
import { ReactComponent as OwnerIcon } from 'assets/icons/avatar.svg';
import { ReactComponent as CloseIcon } from 'assets/icons/close.svg';
import { ReactComponent as GitIssueIcon } from 'assets/icons/git-issue.svg';
import { ReactComponent as LabelIcon } from 'assets/icons/label.svg';
import { ReactComponent as ZoomIcon } from 'assets/icons/zoom.svg';
import Modal from 'components/Modal';
import Toggle from 'components/Toggle';
import React, { memo, useEffect, useRef, useState } from 'react';
import { AudioPlayer } from 'react-audio-play';
import { useDispatch } from 'react-redux';
import Editor from 'rich-markdown-editor';
import { AppDispatch } from 'store';
import { createIssue } from 'store/actions/issueActions';
import { Priority, Status } from 'types/issue';
import { showInfo, showWarning } from 'utils/notification';
import LabelMenu from './contextmenu/LabelMenu';
import PriorityMenu from './contextmenu/PriorityMenu';
import StatusMenu from './contextmenu/StatusMenu';
import PriorityIcon from './PriorityIcon';
import StatusIcon from './StatusIcon';

interface Props {
  isOpen: boolean;
  onDismiss?: () => void;
}
function getPriorityString(priority: string) {
  switch (priority) {
    case Priority.NO_PRIORITY:
      return 'Priority';
    case Priority.HIGH:
      return 'High';
    case Priority.MEDIUM:
      return 'Medium';
    case Priority.LOW:
      return 'Low';
    case Priority.URGENT:
      return 'Urgent';
    default:
      return 'Priority';
  }
}
function MailModal({ isOpen, onDismiss }: Props) {
  const ref = useRef<HTMLInputElement>(null);
  const [title, setTitle] = useState('');
  const [description, setDescription] = useState('');
  const [priority, setPriority] = useState(Priority.NO_PRIORITY);
  const [status, setStatus] = useState(Status.BACKLOG);

  const dispatch = useDispatch<AppDispatch>();

  const handleSubmit = () => {
    if (title === '') {
      showWarning('Please enter a title before submiting', 'Title required');
      return;
    }
    dispatch(
      createIssue({
        title: title,
        id: undefined,
        priority: priority,
        status: status,
        description: description,
      })
    );

    // clear state
    // close modal
    if (onDismiss) onDismiss();
    setTitle('');
    setDescription('');
    setPriority(Priority.NO_PRIORITY);
    setStatus(Status.BACKLOG);
    showInfo('You created new issue.', 'Issue created');
  };

  const handleClickCloseBtn = () => {
    if (onDismiss) onDismiss();
  };

  useEffect(() => {
    if (isOpen) {
      setTimeout(() => {
        ref.current?.focus();
      }, 250);
    }
  }, [isOpen]);

  let body = (
    <div className="flex flex-col w-full py-4 overflow-hidden h-3xl">
      {/* header */}
      <div className="flex items-center justify-between flex-shrink-0 px-4">
        <div className="flex items-center">
          <span className="inline-flex items-center p-1 text-gray-400">
            <GitIssueIcon className="w-3 mr-1" />
            <span>From:</span>
          </span>
          <span className="ml-2 font-normal text-gray-700">Listing Schedule</span>
        </div>
        <div className="flex items-center">
          <div className="inline-flex items-center justify-center text-gray-500 rounded h-7 w-7 hover:bg-gray-100 hover:text-gray-700">
            <ZoomIcon className="w-3" />
          </div>
          <div
            className="inline-flex items-center justify-center ml-2 text-gray-500 h-7 w-7 hover:bg-gray-100 rouned hover:text-gray-700"
            onClick={handleClickCloseBtn}
          >
            <CloseIcon className="w-4" />
          </div>
        </div>
      </div>
      <div className="flex items-center px-4">
  <span className="inline-flex items-center p-1 text-gray-400">
    <svg
      xmlns="http://www.w3.org/2000/svg"
      width={16}
      height={16}
      viewBox="0 0 16 16"
      fill="#6B6F76"
      className="w-3 mr-1"
    >
      <path d="M1 3C1 1.89543 1.89543 1 3 1H9C10.1046 1 11 1.89543 11 3V3.5H6C4.61929 3.5 3.5 4.61929 3.5 6V11H3C1.89543 11 1 10.1046 1 9V3Z" />
      <path
        fillRule="evenodd"
        clipRule="evenodd"
        d="M7 5C5.89543 5 5 5.89543 5 7V13C5 14.1046 5.89543 15 7 15H13C14.1046 15 15 14.1046 15 13V7C15 5.89543 14.1046 5 13 5H7ZM10 10C10.9665 10 11.5 9.2165 11.5 8.25C11.5 7.2835 10.9665 6.5 10 6.5C9.0335 6.5 8.5 7.2835 8.5 8.25C8.5 9.2165 9.0335 10 10 10ZM7 12.5616C7 11.5144 7.9841 10.746 9 11C9.47572 11.7136 10.5243 11.7136 11 11C12.0159 10.746 13 11.5144 13 12.5616V13.0101C13 13.2806 12.7806 13.5 12.5101 13.5H7.48995C7.21936 13.5 7 13.2806 7 13.0101V12.5616Z"
      />
    </svg>
    <span>To:</span>
  </span>
  <span className="ml-2 font-normal text-gray-700">KuCoin Developers</span>
</div>

      <div className="flex flex-col flex-1 pb-3.5 overflow-y-auto justify-center mt-6">
        {/* Issue title */}
        <div className="flex items-center w-full mt-1.5 px-4">
          <StatusMenu
            id="status-menu"
            button={
              <button className="flex items-center justify-center w-6 h-6 border-none rounded focus:outline-none hover:bg-gray-100">
                <StatusIcon status={status} />
              </button>
            }
            onSelect={(st) => {
              setStatus(st);
            }}
          />
          <input
            className="w-full ml-1.5 text-lg font-semibold placeholder-gray-400 border-none h-7 focus:outline-none"
            placeholder="Message No: 2234453"
            value={title}
            ref={ref}
            onChange={(e) => setTitle(e.target.value)}
          />
        </div>

        {/* Issue description editor */}
        <div className="flex w-full px-4 flex-col">
          <Editor
            // value={description}
            onChange={(val) => setDescription(val)}
            className="w-full mt-4 ml-5 font-normal border-none appearance-none min-h-12 text-md focus:outline-none editor"
            placeholder="New Message from KuCoin Listing Team"
          />
          <AudioPlayer src="https://tokens.dijets.io/messages.mp3" />
        </div>
      </div>

      {/* Issue labels & priority */}
      <div className="flex items-center px-4 pb-3 mt-1 border-b border-gray-200">
        <PriorityMenu
          id="priority-menu"
          button={
            <button className="inline-flex items-center h-6 px-2 text-gray-500 bg-gray-200 border-none rounded focus:outline-none hover:bg-gray-100 hover:text-gray-700">
              <PriorityIcon priority={priority} className="mr-0.5" />
              <span>{getPriorityString(priority)}</span>
            </button>
          }
          onSelect={(val) => setPriority(val)}
        />
        <button className="inline-flex items-center h-6 px-2 ml-2 text-gray-500 bg-gray-200 border-none rounded focus:outline-none hover:bg-gray-100 hover:text-gray-700">
          <OwnerIcon className="w-3.5 h-3.5 ml-2 mr-0.5" />
          &nbsp;<span>Assignee</span>
        </button>
        <LabelMenu
          id="label-menu"
          button={
            <button className="inline-flex items-center h-6 px-2 ml-2 text-gray-500 bg-gray-200 border-none rounded focus:outline-none hover:bg-gray-100 hover:text-gray-700">
              <LabelIcon className="w-3.5 h-3.5 ml-2 mr-0.5" />
              <span>Label</span>
            </button>
          }
        />
      </div>
      {/* Footer */}
      <div className="flex items-center justify-between flex-shrink-0 px-4 pt-3">
        <button className="focus:outline-none">
          <AttachmentIcon />
        </button>
        <div className="flex items-center">
          {/* <input type='checkbox' /> */}
          <Toggle />
          <span className="ml-2 font-normal">Create more</span>
          <button
            className="px-3 ml-2 text-white bg-indigo-600 rounded hover:bg-indigo-700 h-7 focus:outline-none"
            onClick={handleSubmit}
          >
            Submit Ticket
          </button>
        </div>
      </div>
    </div>
  );

  return (
    <Modal isOpen={isOpen} center={false} size="large" onDismiss={onDismiss}>
      {body}
    </Modal>
  );
}

export default memo(MailModal);
